body {
  color: #fff;
  background-color: #fff;
  min-width: 360px;
  min-height: 100vh;
  margin: 0;
  position: relative;
  overflow-x: hidden;
}

.container {
  max-width: 1920px;
  padding-left: var(--side-padding);
  padding-right: var(--side-padding);
  margin: 0 auto;
}

.main {
  position: relative;
  overflow: hidden;
}

.decor-left:before {
  content: "";
  z-index: -5;
  background-color: #f1f3f580;
  border-radius: 297px 297px 297px 0;
  width: 1006px;
  height: 594px;
  position: absolute;
  top: 475px;
  left: 0;
}

.decor-right:after {
  content: "";
  z-index: -5;
  background-color: #f1f3f580;
  border-radius: 297px 0 297px 297px;
  width: 1006px;
  height: 594px;
  position: absolute;
  bottom: 470px;
  right: 0;
}

.btn {
  letter-spacing: .06rem;
  color: #333136;
  text-transform: uppercase;
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  max-width: 152px;
  padding: 18px 25px;
  font-size: .95rem;
  font-weight: 500;
  transition: color .3s ease-in-out, background-color .3s ease-in-out;
}

.hidden {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.leaflet-top {
  display: none;
}

.overlay {
  pointer-events: none;
  z-index: 1500;
  opacity: 0;
  background-color: #00000080;
  transition: opacity .3s ease-in-out;
  display: none;
  position: absolute;
  inset: 96px 0 0;
}

.successful {
  width: 450px;
  top: 24px;
  right: var(--side-padding);
  opacity: 0;
  z-index: 1;
  background-color: #e7816b;
  border-radius: 13px;
  flex-direction: column;
  padding: 29px;
  animation: .8s forwards successfulFadeIn;
  display: none;
  position: fixed;
}

.successful__title {
  color: var(--color-white);
  margin-bottom: 12px;
  padding-left: 30px;
  font-size: 1.15rem;
}

.successful__message {
  color: var(--color-green-200-lighter);
  font-size: .97rem;
}

@keyframes successfulFadeIn {
  0% {
    opacity: 0;
    top: 60px;
  }

  100% {
    opacity: 1;
    top: 24px;
  }
}

@media (hover: hover) {
  .btn:hover {
    color: #fff;
    background-color: #ffad9b;
  }
}

a:focus-visible, button:focus-visible {
  outline-offset: 2px;
  outline: 3px solid gray;
}

@media (width <= 1023px) {
  .decor-left:before, .decor-right:after {
    display: none;
  }
}

@media (width <= 767px) {
  .container--mobile {
    padding-left: var(--side-padding-mobile);
